@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap);
/*Developed for Rapsol Technologies as Assignment by:Chrisbin Thomas*/

*{
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
*::selection{
    background: none;
}
*::-moz-selection{
    background: none;
}
body{
    margin:0px;
    padding: 0px;
    
    background-color: #ffffff;
    font-family: 'Montserrat','Poppins', sans-serif;
}
*{
    box-sizing:border-box;
}
ul{
    list-style: none;
}
a{
    text-decoration: none;
}
button{
    outline: none;
    border: none;
}
input{
    outline: none;
    border: none;
}
.logo img{
    width: 120px;
}
.header{
    width:100%;
    height:768px;
    margin-top: 1px;
    position: relative;
    background-repeat: no-repeat;
    background-size:cover;
    background-image: url(/KNS-React-App/static/media/city_print.7fbe4c4f.gif);
}
nav{
	display: flex;
    justify-content: space-around;
	align-items: center;
	left: 0;
	top: 0;
	width:100%;
	z-index: 1;
    background:linear-gradient(90deg,#fdfcfc 40%,#fff6da);
    box-shadow:1px 0.1px 15px rgb(219, 219, 219);
}
nav ul{
	display: flex;
}
.active{
    background-color:rgb(255, 198, 64);
    color:rgb(0, 0, 0);
	box-shadow: 5px 10px 20px rgba(226, 226, 226, 0.616);
	transition: all ease 0.2s;
}
nav ul li a{
	height:40px;
	line-height: 43px;
	margin: 3px;
	padding: 0px 22px;
	display: flex;
	font-size: 0.8rem;
	text-transform: uppercase;
	font-weight: 500;
	color:#242424;
    letter-spacing: 1px;
    border-radius: 3px;
    transition: 0.25s ease-in-out;
}
.property{
    font-family: 'Poppins','calibri';
	height:34px;
    margin-right: 2.5px;
	line-height: 35px;
	padding: 0 20px;
	display: flex;
	font-size: 0.7rem;
	text-transform: none;
    text-shadow: 0px 0px 0.5px rgb(73, 73, 73);
	font-weight: 400;
	color:#ffffff;
    letter-spacing: 1px;
    border-radius: 30px;
    transition: 0.2s ease-in-out;
    background-color: #ffbe25;
}
.property:hover{
    color:#242424;
    background-color: rgb(255, 198, 64);
    transition: ease-in 0.3s;
    text-shadow: 0px 0px 0px #000;
    box-shadow: 0 1.5px  rgba(73, 73, 73, 0.616);
}
.property:active{
    background-color: #ffe676;
    transition: none;
    box-shadow: 0 2px  rgba(73, 73, 73, 0.616);
}
nav ul li a:hover{
	background-color: #f8f8f8;
	box-shadow: 0px 0px 5px rgba(255, 175, 25, 0.904);
	transition: ease-in-out 0.25s;
}
nav ul li a:active{
	background-color: #ff9e2f;
    color:#ffffff;
	box-shadow: 5px 1px 15px rgba(255, 186, 24, 0.616);
    text-decoration: none;
}
nav .menu-btn,
.menu-icon{
		display:none;
	}
/*&Intro&*/
.intro{
	font-family: 'Montserrat','Poppins';
	width: 500px;
	position:absolute;
	left: 16%;
	top: 80%;
    text-align: left;
	-webkit-transform: translate(-20%,-50%);
	        transform: translate(-20%,-50%);
}
.intro span{
    color: #ff9e2f;
}
.intro .details{
    color: #132433;
    background:linear-gradient(360deg,#fafafa 50%,#fff6df);
    border-radius: 7px;
    opacity: 0.8;
}
.intro .details2{
    color: #132433;
    background:linear-gradient(180deg,#fafafa 50%,#eaffdc);
    border-radius: 7px;
    opacity: 0.8;
}
.intro h1{
	font-family:'Montserrat';
	font-size: 45px;
	margin:0px;
	letter-spacing: 3px;
	color:hsl(224, 60%, 47%);
}
.intro p{
    color: #573f06; 
    font-weight: 500;
    text-align:left;
}
.header-btn{
	width:110px;
	height: 40px;
	display: flex;
	justify-content: center;
    align-items: center;
    background-color: #ff9e2f;
    box-shadow: 1px 1.5px 20px rgba(255, 175, 25, 0.904);
    border-radius: 5px;
	color:#ffffff;
    text-shadow:0px 0px 0px  #000;
}
.header-btn:hover{
    transition: all ease 0.5s;
    color: #000000;
    border-radius: 10px;
    box-shadow: 5px 0px 30px rgba(255, 225, 144, 0.705);
}
/*$Howitwork$*/
.container {
    max-width: 1140px;
    margin: 0 auto;
    padding: 0 30px;
}
.flex {
    display: flex;
    justify-content: space-between;
}
.how-it-works {
    padding: 3rem 0;
    background:linear-gradient(120deg,#ffbb00af 10%,#ffb300);
    text-align: center;
}

.how-it-works h2 {
    margin-bottom: 3rem;
    font-size: 30px;
    font-family: 'Montserrat','Poppins';
    color: #ffffff;
    font-weight: 600;
    text-transform: uppercase;
    text-align: center;
}

.how-it-works .flex > div {
    width: 30%;
}

.how-it-works h4 {
    color: #ffffff;
    margin-top: 1rem;

}
.how-it-works p{
    color: rgb(255, 232, 155);
    text-align: left;
    text-justify:inter-word;
}

.how-it-works span.fas {
    color: #ffffff;
    font-size: 35px;
}
/*$About$*/
  .about{
	width: 100%;
	height: 450px;
	background-color:#fbf9f9;
	box-sizing: border-box;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 50px 5% 0px 5%;
	position: relative;
}
.about-text{
	width: 50%;
}
.about-text h2{
	font-size: 2rem;
	color:#236c7e;
	font-weight: 400;
}
.about-text h2{
	margin: 0px;
	padding: 0px;
}
.about-text p{
	font-size: 1.2rem;
	color:#3a3a3a;
    text-align: left;
	
}
.about-text button{
    position: inherit;
	width:140px;
	height: 40px;
	display: flex;
	justify-content: center;
    align-items: center;
    background-color: #ff9e2f;
    box-shadow: 1px 1.5px 20px rgba(255, 175, 25, 0.904);
    border-radius: 5px;
	color:#ffffff;
    text-shadow:0px 0px 0px  #000;
}
.about-text button:hover{
    transition: all ease 0.5s;
    color: #000000;
    border-radius: 10px;
    box-shadow: 5px 0px 30px rgba(255, 225, 144, 0.705);
}
.about-model img{
    height: 100px;
    width: 100%;
    opacity: 0.7;
}
/*$Agents$*/
.agent{
    width:100%;
    height:100vh;
    box-sizing: border-box;
    font-family: 'Montserrat','Poppins';
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 40px ;
    background:linear-gradient(120deg,#ffbb00a1 10%,#ffb300);
}

.a-heading h1{
    color:#ffffff;
    font-size: 2rem;
    margin: 0px;
    padding: 0px;

}
.a-heading p{
    color:rgb(255, 230, 170);
    font-size: 1rem;
    margin: 0px;
    padding: 0px;
}
.a-heading{
    text-align: center;
    margin: 20px 0px;
}
.a-box{
    background-color: #376183;
    width:200px;
    height: 380px;
    margin: 20px;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 20px 10px 10px rgba(0,0,0,0.1);
    position: relative;
}
.b-container{
    display:flex;
    justify-content: center;
    align-content: center;
    width: 100%;
}
.a-b-img{
    width: 100%;
    height: 70%;
    opacity: 0.9;

}
.a-b-img img{
    width:100%;
    height: 100%;
    object-fit: contain;
}
.a-b-text{
    width: 100%;
    height: 30%;
    background-color:#fafafa;
    display:flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
}
.a-box:hover{
    -webkit-transform:translateY(-15px);
            transform:translateY(-15px);
    transition: all ease 0.4s;
    box-shadow: 1px 1px 20px rgba(0, 0, 0, 0.1);

}
.agent-btn{
	width:110px;
	height: 40px;
	display: flex;
	justify-content: center;
    align-items: center;
    background-color: #ff9e2f;
    box-shadow: 5px 10px 30px rgba(2, 82, 255, 0.137);
    border-radius: 10px;
	color:#ffffff;
}
.agent-btn:hover{
	background-color: #ffc889;
    color: #000;
    transition: ease-out 0.7s;
    box-shadow: 5px 0px 30px rgba(255, 158, 2, 0.678);
}
/*$product$*/
.product{
	width: 100%;
	background:linear-gradient(120deg,#f0f0f0 20%,#ffe9b4);
	display: flex;
	height: 280px;
	flex-direction: column;
	align-items: center;
	padding:10px 20px;
	position: relative;
	border-radius: 10px;
	margin-top: 20px;
}
 
.p-heading{
	margin:30px;
	text-shadow: 2px 2px 10px rgba(0,0,0,0.05);
}
.p-heading h3{
	font-weight: 500;
	letter-spacing: 2px;
	text-align: center;
	font-size:2rem;
	color:#433d32;
}
.p-heading p{
	color:rgb(85, 85, 85);
    text-align: center;
}
.product-container{
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	margin: 5px 0px;
	width:100%;
    background:linear-gradient(360deg,#e6e6e6 20%,#ffe5a9);
    border-radius: 20px;
}
.p-box{
	width:300px;
	height: 330px;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	border-radius: 12px;
	position: relative;
	margin: 10px 10px;
    background-color: rgb(255, 248, 223);
    box-shadow: 8px 5px 10px rgba(0,0,0,0.1);
}
.p-box img{
	height:110px;
    opacity: 0.9;
}
.p-box p{
	color:#4d4d4d70;
	font-family: 'Montserrat','Poppins';
	font-size: 0.9rem;
	letter-spacing: 0.5;
}
.price{
	color:#645200;
	font-family:'Montserrat','Poppins';
	font-size: 1rem;;
}
.buy-btn{
	position: absolute;
	width:140px;
	height: 40px;
	display: flex;
	justify-content: center;
	align-items: center;
	font-family:'Montserrat','Poppins';
	left: 50%;
	bottom: 20px;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
	border-radius: 15% 15% 15% 15% / 50% 50% 50% 50%;
	background:linear-gradient(120deg,#96440e 20%,#ffbb00);
	color:#f6f6f6;
	display: none;
	-webkit-animation: fade 0.2s;
	        animation: fade 0.2s;
    box-shadow: 5px 0px 30px rgba(255, 187, 0, 0.897);
}
.p-box:hover .buy-btn{
	display:flex;
}
.p-box:hover{
	box-shadow: 7px 0 7px rgba(0, 0, 0, 0.233);
	background:linear-gradient(180deg,#eeefff 20%,#ffe9ad);
}
/*Developed for Rapsol Technologies as Assignment by:Chrisbin Thomas*/
.p-box:hover .price{
	color:#a38500;
	transition: all ease 0.2s;
}
 
@-webkit-keyframes fade{
	0%{
		opacity: 0;
	}
	100%{
		opacity: 1;
	}
}
 
@keyframes fade{
	0%{
		opacity: 0;
	}
	100%{
		opacity: 1;
	}
}
/*$Contact$*/
.contact{
    background-color: hsl(224, 60%, 47%);
	display:flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
    margin:10px 0px;
    margin-top: 330px;
    padding: 30px 0px;
}
.contact h3{
	font-size:2.5rem;
    margin:20px;
    color: #ffffff;
}
.contact p{
    color:#ffbf2a;
}
.contact-input{
	width:350px;
	height:50px;
	background-color:#FFFFFF;
	display:flex;
	justify-content: center;
	border-radius: 50px;
	box-shadow: 2px 2px 30px rgba(0,0,0,0.15);
}
.contact-input input{
	width:100%;
	background-color: transparent;
	border:none;
	outline: none;
	text-align: center;
	color:#242425;
}
.contact-input a{
	width:200px;
	height:35px;
	background-color:#ff9e2f;
	color:#FFFFFF;
	display: flex;
	font-size: 0.9rem;
	justify-content: center;
	align-items: center;
	margin: auto 10px;
	border-radius: 20px;
	font-weight: 500;
}
.contact-input a:active{
    color:#000;
    background-color: #ffd35a;
    box-shadow:0px 0px 5px #000;
}

/*footer*/
.left-nav{
    width:60%;
    display:flex;
	align-items:center;
    font-size: 12px;
    margin-left: 10px;
}
.copyright{
    margin-left: 190px;
    text-align: center;
    color: #6b4417;
}
.left-nav .logo{
	width:25%;
    text-align:center;
    margin:0px 1%;
    font-size: smaller;
}
.right-nav{
    width:40%;
    text-align:right;
    margin-right:10px;
    font-size: smaller;
}
.footer{
	width:100%;
	height: 16vh;
	display:flex;
	justify-content:space-around;
	align-items:center;
	text-transform: uppercase;
	background-color: #eb8a1c;	
	color:rgb(219, 218, 218);
}
.right-nav .menu a{
	text-decoration:none;
	color:rgb(219, 218, 218);
    margin-right:10px;
}
@media(max-width:1190px){
	.header{
        background-size: 1150px !important;
        text-align: left;
	}
	.intro{
		left: 10%;
		top: 50%;
		-webkit-transform: translate(-10%,-50%);
		        transform: translate(-10%,-50%);
	}
	.about-model img{
		height:400px;
    }
    .agent{
		height:auto;
    }
    .b-container{
		flex-wrap: wrap;
	}
    .a-box{
		flex-grow: 1;
	}
	.a-b-img img{
		object-fit:contain;
	}

}

@media(max-width:970px){
	.main{
		background-image: none !important;
		
	}
	.intro{
		left: 50%;
		top: 50%;
		-webkit-transform: translate(-50%,-50%);
		        transform: translate(-50%,-50%);
    }
    .header-btn{
        margin-left: 30%;
    }
	.about-model{
		display: none;
	}
	.about{
        text-align: center;
		justify-content: center;
		padding-top: 0px;
	}
	.about-text{
		width: 90%;
	}
	.about-text h1{
		font-size:4rem;
    }
	
}

@media(max-width:600px){
	.intro{
		width:60%;
    }}
    .scroll{
        left: 42.5%;
    }
    @media (max-width: 500px) {
        .how-it-works .flex {
            display: block;
        }
    
        .how-it-works .flex > div {
            width: 100%;
            margin-top: 2rem;
        }
        .contact{
            margin-top: 1000px;
        }
    
@media(max-width:1100px){.menu-icon{
    display:block;}
        nav{
            justify-content: space-between;
            height: 65px;
            padding: 0px 30px;
            position: fixed;
        }
        .property{
            background: none;
            font-size: 13px;
            color: #000;
            padding: 0px;
        }
        .logo img{
            width:70px;
        }
        .menu{
            display:none;
            position:absolute;
            top:65px;
            left:0px;
            background-color:#FFFFFF;
            border-bottom:4px solid #064757;
            width:100%;
            padding:0px;
            margin:0px;
        }
        .menu li{
            width:100%;
        }
        nav .menu li a{
            width:100%;
            height:40px;
            justify-content: center;
            align-items: center;
            margin:0px;
            padding: 25px;
            border:1px solid rgba(38,38,38,0.03);
        }
        nav .menu-icon{
            cursor:pointer;
            float:right;
            padding:28px 20px;
            position:relative;
            -webkit-user-select: none;
                -ms-user-select: none;
                    user-select: none;
        }
        nav .menu-icon .nav-icon{
            background-color:#333333;
            display:block;
            height:2px;
            position:relative;
            transition: background 0.2s ease-out;
            width:20px;
        }
        nav .menu-icon .nav-icon:before,
        nav .menu-icon .nav-icon:after{
            background:#333;
            content:'';
            display:block;
            height:100%;
            position:absolute;
            transition:all ease-out 0.2s;
            width:100%;
        }
        nav .menu-icon .nav-icon:before{
            top:5px;
        }
        nav .menu-icon .nav-icon:after{
            top:-5px;
        }
        nav .menu-btn:checked ~ .menu-icon .nav-icon{
            background:transparent;
        }
        nav .menu-btn:checked ~ .menu-icon .nav-icon:before{
            -webkit-transform: rotate(-45deg);
                    transform: rotate(-45deg);
            top:0;
        }
        nav .menu-btn:checked ~ .menu-icon .nav-icon:after{
            -webkit-transform: rotate(45deg);
                    transform: rotate(45deg);
            top:0;
        }
        nav .menu-btn{
            display:none;
        }
        nav .menu-btn:checked ~ .menu{
            display:block;
        }}
        @media(max-width:400px){
            .s-box{
                width: 100%;
                height:400px;
            }
        }}
